<template>
    <notification :warning="notifa.warning" :success="notifa.success" :text="notifa.text"></notification>
    <div class="leftMenuContainer">
        <div class="leftMenu">
            <ul>
                <li :class="{ active:checkmenu.all }" @click="menuto('all')">全部事务</li>
                <li :class="{ active:checkmenu.unreplied }" @click="menuto('unreplied')">待回复事务</li>
                <li :class="{ active:checkmenu.replied }" @click="menuto('replied')">已回复事务</li>
            </ul>
        </div>
    </div>
    <div class="mainBodyContainer">
        <div class="searchBar">
            <input type="text" placeholder="id、名称、内容、用户" v-model="search">
            <button :class="{ able: sBtnAble }" @click="searchNow">搜索</button>
        </div>
        <div class="listHead">
            <div class="num">序号</div>
            <div class="title">标题</div>
            <div class="user">咨询人</div>
            <div class="tools">操作</div>
        </div>
        <div class="listItem" v-for="item in issues">
            <div class="num" @click="replyNow(item.id)">{{ item.id }}</div>
            <div class="title" @click="replyNow(item.id)">{{ item.title }}</div>
            <div class="user" @click="replyNow(item.id)">{{ item.user | empty }}</div>
            <div class="tools">
                <i class="fa fa-reply" @click="replyNow(item.id)"> 回复</i>
                <i class="fa fa-trash-o" @click="deleteIssue(item.id)" v-if="user.rank >= 5"> 删除</i>
            </div>
        </div>
        <div class="fetchMore">
            <p @click="fetchMore">点击获取更多...</p>
        </div>
    </div>
    <div class="editMask" v-show="replyOpen" transition="fade">
        <div class="edit">
            <div class="editTitle">
                回复事务
                <span @click="closeReply"><i class="fa fa-times-circle"></i></span>
            </div>
            <div class="editBody">
                <div class="item">
                    <div class="left">标题</div>
                    <div class="right">{{ replyFor.title }}</div>
                </div>
                <div class="item">
                    <div class="left">事务内容</div>
                    <div class="right">{{ replyFor.content }}</div>
                </div>
                <div class="item">
                    <div class="left">咨询人</div>
                    <div class="right">{{ replyFor.studentName }}</div>
                </div>
                <div class="item">
                    <div class="left">咨询人学号</div>
                    <div class="right">{{ replyFor.studentNo }}</div>
                </div>
                <div class="item">
                    <div class="left">所在单位</div>
                    <div class="right">{{ replyFor.org }}</div>
                </div>
                <div class="item">
                    <div class="left">联系方式</div>
                    <div class="right">{{ replyFor.phone }}</div>
                </div>
                <div class="item">
                    <div class="left">回复部门</div>
                    <div class="right">
                        <input type="text" v-model="reply.asso">
                    </div>
                </div>
                <div class="item">
                    <div class="left">回复内容</div>
                    <div class="right" v-if="parseInt(user.rank) >= 5 || replyFor.reply === ''">
                        <textarea placeholder="回复些什么吧" v-model="reply.reply">{{ replyFor.reply }}</textarea>
                        <span class="inputTips">可以输入&lt;br&gt;、&lt;img&gt;、&lt;b&gt;、&lt;font&gt;等HTML标签</span>
                    </div>
                    <div class="right" v-else>
                        {{ replyFor.reply }}
                    </div>
                </div>
                <div class="item">
                    <div class="left">回复人</div>
                    <div class="right">{{ replyFor.responder }}</div>
                </div>
                <div class="item">
                    <div class="replyBtn">
                        <button :class="{ able: btnAble }" @click="pushReply">提交回复</button>
                        <span @click="closeReply">取消</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import notification from '../../components/notification.vue'
import store from '../../vuex/store'

export default {
    data () {
        return {
            issues: [{
                id: 1,
                title: 'dr.com自动关闭',
                user: ''
            }],
            replyFor: {
                id: 1,
                title: 'dr.com自动关闭',
                content: '为什么最近dr.com总是隔十几二十分钟就自动关闭，然后就断网了，要重新打开客户端连接。我宿友开学就这样，我这个星期也开始这样了，看网上很多其他同学也说有这样的问题，是校园网出什么问题了吗，有什么解决办法吗',
                studentName: '陈俊毅',
                studentNo: '2014130355',
                org: '深圳大学信息工程学院2014级',
                phone: '13128931074',
                reply: 'dfgh',
                responder: 'jason'
            },
            replyOpen: false,
            btnAble: false,
            sBtnAble: false,
            reply: {
                id: 0,
                asso: '事务中心',
                reply: '',
                replied: false
            },
            notifa: {
                warning: false,
                success: false,
                text: ''
            },
            checkmenu: {
                all: true,
                replied: false,
                unreplied: false
            },
            user: store.state.user,
            search: '',
            page: 1,
            type: 0
        }
    },
    store,
    computed: {
        'btnAble': function () {
            return (this.reply.reply !== '' && this.reply.asso !== '')
        },
        'sBtnAble': function () {
            return (this.search !== '')
        },
        'reply.replied': function () {
            if (this.reply.reply.length === 0) {
                return false
            } else {
                return true
            }
        },
        user () {
            return store.state.user
        }
    },
    methods: {
        replyNow (id) {
            if (window.location.hostname === 'localhost') {
                this.replyOpen = true
            } else {
                this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissueone.php?id=' + id).then(res => {
                    this.replyFor = res.data
                    this.reply.id = this.replyFor.id
                    this.reply.reply = ''
                    this.replyOpen = true
                })
            }
        },
        closeReply () {
            this.replyOpen = false
        },
        pushReply () {
            if (!this.btnAble) return
            this.reply.reply = this.reply.reply.replace(/\n/g, '<br />')
            this.$http.post('http://szdx.sinaapp.com/dashboard/rest/v1/issue/replyissue.php', this.reply)
                    .then(response => {
                        this.replyOpen = false
                        if (response.data === 'success') {
                            this.notifa.text = '回复成功！'
                            this.notifa.success = true
                            setTimeout(() => {
                                this.notifa.success = false
                            }, 2000)
                        } else {
                            this.replyOpen = false
                            this.notifa.text = '回复失败！'
                            this.notifa.warning = true
                            setTimeout(() => {
                                this.notifa.warning = false
                            }, 2000)
                        }
                    }, () => {
                        this.replyOpen = false
                        this.notifa.text = '回复失败！'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    })
        },
        menuto (type) {
            switch (type) {
                case 'all':
                    this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissue.php?type=0').then(res => {
                        this.issues = res.data
                    })
                    this.type = 0
                    this.page = 1
                    this.checkmenu.all = true
                    this.checkmenu.replied = false
                    this.checkmenu.unreplied = false
                    break
                case 'replied':
                    this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissue.php?type=2').then(res => {
                        this.issues = res.data
                    })
                    this.type = 2
                    this.page = 1
                    this.checkmenu.all = false
                    this.checkmenu.replied = true
                    this.checkmenu.unreplied = false
                    break
                case 'unreplied':
                    this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissue.php?type=1').then(res => {
                        this.issues = res.data
                    })
                    this.type = 1
                    this.page = 1
                    this.checkmenu.all = false
                    this.checkmenu.replied = false
                    this.checkmenu.unreplied = true
                    break
            }
        },
        deleteIssue (id) {
            if (window.confirm('你确定要删除编号为：' + id + '的咨询吗？')) {
                this.$http.post('http://szdx.sinaapp.com/dashboard/rest/v1/issue/deleteissue.php?id=' + id)
                    .then(response => {
                        if (response.data === 'success') {
                            this.notifa.text = '删除成功！'
                            this.notifa.success = true
                            setTimeout(() => {
                                this.notifa.success = false
                            }, 2000)
                            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissue.php?type=0').then(res => {
                                this.issues = res.data
                            })
                        } else {
                            this.notifa.text = '删除失败！'
                            this.notifa.warning = true
                            setTimeout(() => {
                                this.notifa.warning = false
                            }, 2000)
                        }
                    }, () => {
                        this.notifa.text = '删除失败！'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    })
            } else {
                return
            }
        },
        searchNow () {
            if (!this.sBtnAble) return
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/searchissue.php?keyword=' + this.search)
                    .then(response => {
                        if (response.data.status === 'success') {
                            this.issues = response.data.data
                        } else if (response.data.status === 'empty') {
                            this.notifa.text = '没有找到相应的结果'
                            this.notifa.warning = true
                            setTimeout(() => {
                                this.notifa.warning = false
                            }, 2000)
                        }
                    }, () => {
                        this.notifa.text = '服务器故障'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    })
        },
        fetchMore () {
            this.page++
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissue.php?type=' + this.type + '&page=' + this.page).then(res => {
                if (res.data.length === 0) {
                    this.page--
                    this.notifa.text = '没有更多啦！！'
                    this.notifa.success = true
                    setTimeout(() => {
                        this.notifa.success = false
                    }, 2000)
                } else {
                    this.issues.push(...res.data)
                }
            }, () => {
                this.page--
                this.notifa.text = '服务器故障'
                this.notifa.warning = true
                setTimeout(() => {
                    this.notifa.warning = false
                }, 2000)
            })
        }
    },
    ready () {
        if (window.location.hostname !== 'localhost') {
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/issue/getissue.php?type=0').then(res => {
                this.issues = res.data
                this.page = 1
            })
        }
    },
    components: {
        notification
    },
    filters: {
        empty (val) {
            return (val === '') ? '　' : val
        }
    }
}
</script>

<style lang="less" scoped>
.leftMenuContainer {
    float: left;
    width: 15%;
    padding: 20px 10px 40px 40px;

    .leftMenu {
        width: 100%;

        ul {
            list-style: none;

            li {
                color: #666;
                margin-bottom: 5px;
                padding: 0 10px;
                font-size: 16px;
                line-height: 30px;
                cursor: pointer;
                user-select: none;

                &:hover {
                    color: #F45757;
                    border-right: 2px solid #F45757;
                }

            }

            .active {
                color: #F45757;
                border-right: 2px solid #F45757;
            }

        }

    }

}

.mainBodyContainer {
    float: left;
    width: 85%;
    height: 90%;
    padding: 20px 40px 40px 10px;
    overflow: auto;

    .searchBar {
        float: left;
        width: 100%;
        margin-bottom: 10px;
        text-align: right;

        input {
            width: 200px;
            padding: 5px 10px;
            color: #2b2b2b;
            font-size: 14px;
            line-height: 20px;
            border-radius: 3px;
            border: 1px solid #ddd;
            transition: all 300ms ease;

            &:focus, &:hover {
                border: 1px solid #F45757;
            }

            &:focus {
                width: 300px;
            }
        }

        button {
            width: 100px;
            height: 32px;
            margin-top: 5px;
            color: #aaa;
            font-size: 14px;
            line-height: 32px;
            background: #F6F6F6;
            border-radius: 3px;
            transition: all 200ms ease;
        }

        .able {
            color: #FFF;
            background: #F45757;
            cursor: pointer;

            &:active {
                background: #d44343;
            }
        }
    }

    .listHead {
        float: left;
        width: 100%;
        background: #F6F6F6;
        font-size: 14px;
        line-height: 40px;
        user-select: none;
        cursor: pointer;
        
        div {
            float: left;
            text-align: center;
            border-right: 1px solid #FFF;
        }

        .num {
            width: 8%;
        }

        .title {
            width: 55%;
        }

        .user {
            width: 22%;
        }

        .tools {
            width: 15%;
        }

    }

    .listItem {
        float: left;
        width: 100%;
        margin-top: 2px;
        font-size: 14px;
        line-height: 40px;
        user-select: none;
        cursor: pointer;
        
        div {
            float: left;
            text-align: center;
            border-right: 1px solid #FFF;
        }

        .num {
            width: 8%;
        }

        .title {
            padding: 0 5px;
            width: 55%;
            display: block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .user {
            width: 22%;
        }

        .tools {
            width: 15%;

            i:hover {
                color: #F45757;
            }

            i:nth-child(1) {
                margin-right: 5px;
            }

        }

        &:hover {
            background: #F9F9F9;
        }

    }

    .fetchMore {
        float: left;
        margin-top: 20px;
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: center;

        p {
            width: 200px;
            margin: 0 auto;
            color: #333;
            cursor: pointer;
            border-radius: 3px;
            user-select: none;
            
            &:hover {
                background: rgba(0, 0, 0, .05);
            }

            &:active {
                background: rgba(0, 0, 0, .1);
            }
        }
    }
}

.editMask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .8);
    z-index: 1005;

    .edit {
        position: fixed;
        top: 50%;
        left: 50%;
        margin-left: -250px;
        margin-top: -270px;
        width: 500px;
        height: 540px;
        overflow: auto;
        background: #FFF;
        border-radius: 5px;
        box-shadow: 0 0 20px rgba(0, 0, 0, .4);

        .editTitle {
            padding: 0 10px;
            color: #FFF;
            font-size: 16px;
            line-height: 36px;
            background: #F45757;

            span {
                float: right;
                cursor: pointer;
            }
        }

        .editBody {
            width: 100%;
            padding: 10px;
            padding-bottom: 20px;

            .item {
                float: left;
                width: 100%;
                margin-top: 5px;

                .left {
                    float: left;
                    width: 20%;
                    padding: 5px 10px;
                    text-align: right;
                    color: #F45757;
                    font-size: 14px;
                }
                .right {
                    float: left;
                    width: 76%;
                    padding: 5px;
                    text-align: justify;
                    color: #2b2b2b;
                    font-size: 14px;

                    input, textarea {
                        width: 100%;
                        padding: 5px 10px;
                        color: #2b2b2b;
                        font-size: 14px;
                        line-height: 24px;
                        border-radius: 3px;
                        border: 1px solid #ddd;
                        transition: border 200ms ease;

                        &:focus, &:hover {
                            border: 1px solid #F45757;
                        }
                    }

                    textarea {
                        height: 200px;
                        resize: none;
                    }
                    
                    .inputTips {
                        color: #999;
                        font-size: 12px;
                        line-height: 12px;
                    }
                }

                .replyBtn {
                    width: 150px;
                    margin: 0 auto;

                    button {
                        width: 100px;
                        height: 32px;
                        margin-top: 5px;
                        color: #aaa;
                        font-size: 14px;
                        line-height: 32px;
                        background: #F6F6F6;
                        border-radius: 3px;
                        transition: all 200ms ease;
                    }

                    .able {
                        color: #FFF;
                        background: #F45757;
                        cursor: pointer;

                        &:active {
                            background: #d44343;
                        }
                    }

                    span {
                        float: right;
                        color: #888;
                        font-size: 14px;
                        line-height: 42px;
                        cursor: pointer;
                        user-select: none;

                        &:hover {
                            color: #F45757;
                        }

                         &:active {
                            color: #d44343;
                        }
                    }
                }   
            }
        }
    }
}

/* 必需 */
.fade-transition {
    transition: all .3s ease;
    opacity: 1;
}

/* .fade-enter 定义进入的开始状态 */
/* .fade-leave 定义离开的结束状态 */
.fade-enter, .fade-leave {
    opacity: 0;
}
</style>



